import { Component, h, State } from '@stencil/core';
import { ListSeparator, MenuItem } from 'src/interface';

/**
 * Hiding or Disabling Irrelevant Buttons in User Interfaces
 *
 */
@Component({
    tag: 'limel-example-button-disabled-vs-hidden',
    shadow: true,
    styleUrl: 'button-disabled-vs-hidden.scss',
})
export class ButtonDisabledVsHideExample {
    @State()
    private value: boolean = false;

    @State()
    private clicked: boolean = false;

    @State()
    public required: boolean;

    private items: Array<ListSeparator | MenuItem> = [
        { text: 'Edit', icon: 'save_as' },
    ];

    public render() {
        return [
            <h3>Disabled vs. Hidden</h3>,
            <p>
                Effective use of 'Disabled' and 'Hidden' buttons is crucial in
                user interface design, and the choice between the two should
                always be context-dependent. These guidelines provide insights
                into when to apply each approach to optimize user experiences.
                <br />
                <br />
                <b> Disabling a Button:</b>
            </p>,
            <p>
                Use button disabling when the action the button represents is
                not currently available but may become available in the future.
                For example, you can disable a "Submit" button until all
                required form fields are filled out.
            </p>,
            <div class="do">
                <limel-header icon="ok" heading="Disabled button in the form" />
                <limel-checkbox
                    label="Accept terms and conditions"
                    required
                    onChange={this.setChecked}
                    checked={this.value}
                />
                <limel-button
                    label="Submit"
                    disabled={this.value ? false : true}
                />
            </div>,
            <p>
                It's helpful when you want to communicate to the user why they
                can't perform a certain action. Disabled buttons could have a
                tooltip or message explaining why they are disabled.
            </p>,

            <div class="do">
                <limel-header
                    icon="ok"
                    heading="Disabled button together with a Tooltip"
                />
                <div id="tooltip-example">
                    <limel-button
                        class="disabled-button"
                        disabled
                        icon="phone"
                        label="Call"
                    />
                </div>
                <limel-tooltip
                    label="Select recipient to make a call"
                    elementId="tooltip-example"
                />
            </div>,
            <p>
                It can be suitable for indicating to the user that they need to
                complete a certain step or meet specific conditions before
                proceeding.
            </p>,
            <div class="do">
                <limel-header
                    icon="ok"
                    heading="Disabled button together with an info icon"
                />
                <div class="button-icon">
                    <limel-button
                        class="disabled-button"
                        disabled
                        icon="plus_math"
                        label="Add recipient"
                    />
                    <limel-icon name="info" id="tooltip" size="x-small" />
                </div>
                <limel-tooltip
                    label="To enable the feature call our support"
                    elementId="tooltip"
                />
            </div>,
            <h3>Hiding a Button:</h3>,
            <p>
                Hide the button if it's not relevant to a context. If the user
                is already assigned there is no sense in having the assign
                button, it is better to show the <b> unassign</b> button
                instead.
            </p>,
            <div class="do">
                <limel-header icon="ok" heading="Show only a relevant button" />
                <limel-button
                    class="relevant-button"
                    icon={this.clicked ? '' : 'whole_hand_right'}
                    label={this.clicked ? 'Unassign me' : 'Assign me'}
                    onClick={this.isClicked}
                />
            </div>,
            <p>
                When an action is permanently unavailable or not allowed for a
                particular user role (e.g., an admin-only action), it's best to
                hide the button rather than disabling it.
            </p>,
            <div class="split-example">
                <div class="do">
                    <limel-header
                        icon="admin_settings"
                        heading="Admin Interface"
                    />
                    <limel-split-button
                        label="Save"
                        icon="save"
                        items={this.items}
                        onSelect={this.handleSelect}
                    />
                </div>
                <div class="do">
                    <limel-header icon="person_male" heading="User Interface" />
                    <limel-split-button label="Save" icon="save" />
                </div>
            </div>,
        ];
    }

    private handleSelect = (event: CustomEvent<MenuItem>) => {
        console.log('Menu item chosen', event.detail.text);
    };

    private setChecked = (event: CustomEvent<boolean>) => {
        event.stopPropagation();
        this.value = event.detail;
    };

    private isClicked = () => {
        this.clicked = !this.clicked;
    };
}
